export default{
  data() {
    return {
      tabsList: ['未使用', '已使用', '已过期'],
      tabData: 0,
      discount: {
        word: '还没有优惠券哦',
        info: '快去领券中心领优惠券吧~'
      },
      imgName: 'usable',
      btnShow: 'visible'
    }
  },
  watch: {
    tabData(val) {
      if (val !== 0) {
        this.imgName = 'unusable'
        this.btnShow = 'hidden'
        return
      }
      this.imgName = 'usable'
      this.btnShow = 'visible'
    }
  },
  computed: {
    statusBarHeight() {
      return this.$store.state.statusBarHeight
    }
  },
  methods: {
    goBack() {
      uni.navigateBack({
        delta: 1
      })
    },
    goMean() {
      uni.navigateTo({
        url: 'mean'
      })
    },
    selectTab(index) {
      this.tabData = index
    },
    goDiscountCenter() {
      uni.navigateTo({
        url: 'discountCenter'
      })
    }
  }
}
